import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FlashMessageModule } from 'angular-flash-message';

import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { HomeComponent } from './components/home/home.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { MessagesComponent } from './components/messages/messages.component';
import { SearchComponent } from './components/search/search.component';

import { ProfileComponent } from './components/profile/profile.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';

import { TaskComponent } from './components/dashboard/task/task.component';
import { AssignmentComponent } from './components/dashboard/assignment/assignment.component';
import { ReportComponent } from './components/dashboard/report/report.component';
import { CreateComponent } from './components/dashboard/create/create.component';

import { AuthorizeService } from './services/authorize.service';
import { AuthGuard } from './guards/auth.guard';
import { TaskService } from './services/task.service';
import { ValidateService } from './services/validate.service';
import { MessageService } from './services/message.service';

const appRoutes: Routes = [
	{
    	path: '',
    	component: HomeComponent
  	},
  	{
	    path: 'dashboard',
	    component: DashboardComponent,
		canActivate: [AuthGuard],
		children: [
			{
				path: 'tasks',
    			component: TaskComponent,
    			canActivate: [AuthGuard]
			},
			{
				path: 'assignments',
    			component: AssignmentComponent,
    			canActivate: [AuthGuard]
			},
			{
				path: 'report',
				component: ReportComponent,
				canActivate: [AuthGuard]
			},
			{
				path: 'create',
				component: CreateComponent,
				canActivate: [AuthGuard]
			}
		]
  	},
  	{
	    path: 'messages',
	    component: MessagesComponent,
		canActivate: [AuthGuard]
	},
	{
		path: 'search/:keyword',
		component: SearchComponent,
		canActivate: [AuthGuard]
	},
  	{
    	path: 'profile/:emp_id',
    	component: ProfileComponent,
    	canActivate: [AuthGuard]
  	},
  	{
    	path: 'login',
    	component: LoginComponent
  	},
  	{
    	path: 'register',
    	component: RegisterComponent
  	}
];

@NgModule({
	declarations: [
    	AppComponent,
    	NavbarComponent,
    	HomeComponent,
    	DashboardComponent,
		TaskComponent,
		AssignmentComponent,
    	ReportComponent,
    	CreateComponent,
		MessagesComponent,
		SearchComponent,
    	ProfileComponent,
    	LoginComponent,
    	RegisterComponent    
  	],
  	imports: [
    	BrowserModule,
    	FormsModule,
    	HttpModule,
    	HttpClientModule,
    	RouterModule.forRoot(appRoutes),
    	FlashMessageModule
  	],
  	providers: [
    	ValidateService,
    	AuthorizeService,
    	AuthGuard,
    	TaskService,
    	MessageService
  	],
  	bootstrap: [ AppComponent ]
})

export class AppModule { }



