import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { ValidateService } from '../../services/validate.service';
import { AuthorizeService } from '../../services/authorize.service';
import { FlashMessage } from 'angular-flash-message/dist/flash-message';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

	name: String;
	username: String;
	email: String;
	password: String;

	constructor(
		private validate: ValidateService,
		private authorize: AuthorizeService,
		private flashMessage: FlashMessage,
		private router: Router
	) { }

  ngOnInit() {
  }

	onRegisterSubmit() {

		const user = {
			_id: 1004,
			name: this.name,
			username: this.username,
			email: this.email,
			password: this.password
		}

		if (!this.validate.validateRegister(user)) {
			this.flashMessage.danger('Please fill in all the fields', {timeout: 3000});
		}
		else {
			this.authorize.registerUser(user)
			.subscribe(data => {
				if (data.success) {
					this.flashMessage.success('You have registered!', {timeout: 3000});
					this.router.navigate(['login']);
				}
				else {
					this.flashMessage.danger('Something went wrong. Please try again.', {timeout: 3000});
					this.router.navigate(['register']);
				}
			});
		}
	}	
}



