import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { AuthorizeService } from '../../services/authorize.service';
import { MessageService } from '../../services/message.service';
import { FlashMessage } from 'angular-flash-message';

import { Employee } from '../../models/Employee';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})

export class ProfileComponent implements OnInit {

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private authorize: AuthorizeService,
    private messageService: MessageService,
    private flashMessage: FlashMessage
  ) { }

  employee: Employee;
  emp_id: number;
  subject: string;
  message: string;

  ngOnInit() {

    this.route.paramMap.subscribe(params => {
      this.emp_id = +params.get('emp_id');
      this.authorize.getEmployeeData(this.emp_id) 
        .subscribe(employee => {
          this.employee = employee;
        });
    });
  }

  myProfile() {
    if (this.emp_id == +localStorage.getItem('user_id')) {
      return true;
    }
    else {
      return false;
    }
  }

  sendMessage() {
    const newMessage = {
      to: this.emp_id,
      from: +localStorage.getItem('user_id'),
      subject: this.subject,
      message: this.message,
      date: new Date(),
      read: false
    }
    this.messageService.sendMessage(newMessage)
      .subscribe(data => {
        if (data.success) {
          this.flashMessage.success(data.msg, {timeout: 3000});
        }
        else {
          this.flashMessage.danger(data.msg, {timeout: 3000});
        }
      });
    this.subject = '';
    this.message = '';
  }
  
}



