import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AuthorizeService } from '../../../services/authorize.service';
import { ValidateService } from '../../../services/validate.service';
import { TaskService } from '../../../services/task.service';
import { FlashMessage } from 'angular-flash-message/dist/flash-message';

import { User } from '../../../models/User';

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.css']
})
export class CreateComponent implements OnInit {

  constructor(
    private authorize: AuthorizeService,
    private validate: ValidateService,
    private taskService: TaskService,
    private flashMessage: FlashMessage,
    private router: Router
  ) { }

  user: User;
  team: User[];
  who: string;

  emp_id: number;
  mgr_id: number;
  client: string;
  category: string;
  title: string;
  assign: string;
  start: string;
  end: string;
  status: string;

  emp_temp: number;
  mgr_temp: number;

  ngOnInit() {

    $('#me').click(function() {
      $('#assign').val('');
      $('#assign').attr('disabled','disabled');
    });
    $('#them').click(function() {
      $('#assign').removeAttr('disabled');
    });

    this.taskService.getTeam(localStorage.getItem('user_id'))
      .subscribe(data => {
        this.team = data
      },
      err => {
        console.log(err);
      });
      
  }

  clicker() {
    alert(this.emp_id);
  }

  onCreateSubmit() {
    
    if (this.emp_id) {
      this.emp_temp = this.emp_id;
      this.mgr_temp = +localStorage.getItem('user_id');
    }
    else {
      this.emp_temp = +localStorage.getItem('user_id');
    }
    const task = {
      emp_id: this.emp_temp,
      mgr_id: this.mgr_temp,
      client: this.client,
      category: this.category,
      title: this.title,
      start: this.start,
      end: this.end,
      status: "Initialized"
    }

    if (!this.validate.validateTask(task)) {
      this.flashMessage.danger('Please fill in all the fields', {timeout: 3000});
    }
    else {
      this.taskService.createTask(task)
			  .subscribe(data => {
				  if (data.success) {
            this.flashMessage.success(data.msg, {timeout: 3000});
            if (this.mgr_temp) {
              this.router.navigate(['dashboard/report']);
            }
            else {
              this.router.navigate(['dashboard/tasks']);
            }
				  }
				  else {
					  this.flashMessage.danger(data.msg, {timeout: 3000});
					  this.router.navigate(['create']);
				  }
			  });
    }
  }
}


