import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FlashMessage } from 'angular-flash-message';

import { ValidateService } from '../../services/validate.service';
import { AuthorizeService } from '../../services/authorize.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  
  username: String;
  password: String;

  constructor(
    private validate: ValidateService,
    private authorize: AuthorizeService,
    private router: Router,
    private flashMessage: FlashMessage
  ) { }

  ngOnInit() {
  }

  onLoginSubmit() {
    const user = {
      username: this.username,
      password: this.password
    };

    if (!this.validate.validateLogin(user)) {
      this.flashMessage.danger('Please fill in all the fields', {timeout: 3000});
    }
    else {
      this.authorize.authenticateUser(user)
      .subscribe(data => {
        if (data.success) {
          this.authorize.storeUserData(data.token, user, data.user.user_id);
          this.flashMessage.success('Welcome, '+user.username, {timeout: 3000});
          this.router.navigate(['dashboard/tasks']); 
        }
        else {
          this.flashMessage.danger(data.msg, {timeout: 3000});
        }
      });
    }
  }
}



