import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AuthorizeService } from '../../../services/authorize.service';
import { TaskService } from '../../../services/task.service';
import { FlashMessage } from 'angular-flash-message/dist/flash-message';

import { User } from '../../../models/User';
import { Task } from '../../../models/Task';
import { Employee } from '../../../models/Employee';

@Component({
  selector: 'app-task',
  templateUrl: './task.component.html',
  styleUrls: ['./task.component.css']
})
export class TaskComponent implements OnInit {

  constructor(
    private authorize: AuthorizeService,
    private taskService: TaskService,
    private router: Router,
    private flashMessage: FlashMessage
  ) { }

  user: User;
  id: number;
  tasks: Task[];

  ngOnInit() {

    this.id = +localStorage.getItem('user_id');

    this.taskService.getTasks(this.id)
      .subscribe(tasks => {
        this.tasks = tasks;
      });
  
  }

  deleteTask(task_id) {
    var tasks = this.tasks;
    this.taskService.deleteTask(task_id)
      .subscribe(data => {
        for (var i=0 ; i<tasks.length ; i++) {
          if (tasks[i]._id == task_id) {
            tasks.splice(i,1);
          }
        }
      });
      this.flashMessage.success('Task deleted.', {timeout: 3000});
  }

  updateTask(task) {
    this.taskService.updateTask(task)
      .subscribe(data => {
        if (data.success) {
					this.flashMessage.success(data.msg, {timeout: 3000});
				}
				else {
					this.flashMessage.danger(data.msg, {timeout: 3000});
				}
			});
  }

}



