import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AuthorizeService } from '../../../services/authorize.service';
import { TaskService } from '../../../services/task.service';
import { FlashMessage } from 'angular-flash-message/dist/flash-message';

import { User } from '../../../models/User';
import { Employee } from '../../../models/Employee';
import { Task } from '../../../models/Task';

@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.css']
})
export class ReportComponent implements OnInit {

  constructor(
    private authorize: AuthorizeService,
    private taskService: TaskService,
    private router: Router,
    private flashMessage: FlashMessage
  ) { }

  user: User;
  id: number;

  reports: Task[];
  reporter: Employee;

  ngOnInit() {
    this.id = +localStorage.getItem('user_id');

    this.authorize.getProfile()
      .subscribe(profile => {
        this.user = profile.user;
      },
      err => {
        console.log(err);
        return false;
      });

    this.taskService.getReport(this.id)
      .subscribe(reports => {
        this.reports = reports;
      });    
  }

  deleteReport(report_id) {
    var reports = this.reports;
    this.taskService.deleteTask(report_id)
      .subscribe(data => {
        for (var i=0 ; i<reports.length ; i++) {
          if (reports[i]._id == report_id) {
            reports.splice(i,1);
          }
        }
      });
      this.flashMessage.success('Report deleted.', {timeout: 3000});
  }

  viewProfile(name) {
    this.router.navigate(["profile",name]);
  }

}



